@import "../settings/colors";
@import "../settings/fonts";

.formSection{
  height: 790px;
  width: 100%;
  position: relative;
}

 .formSection::before {
   height: 850px;
   width: 100%;
   background-image: url("../../assets/Background-Contact-Form.jpg");
   background-size: cover;
   background-repeat: no-repeat;
   content: '';
   display: block;
   position: absolute;
   z-index: -2;
   top: 0;
   left: 0;
   opacity: .3;
  }
  .rightForm {
    width: 500px;
    height: 500px;
    position: absolute;
    float: right;
    top: 180px;
    right: 50px;
    .decorations {
      height: 30px;
      background-image: url("../../assets/Decoration.svg");
      background-position: center;
      background-repeat: no-repeat;
      margin: 25px;
      color: black;
    }
    span{
      font-size: 10px;
    }
    .formDiv {
      display: block;
      padding: 35px;
      font-family: $font-base;
      font-size: 12px;
      color: $darksilver;
      button {
        margin-top: 65px;
        margin-right: 0;
        float: right;
        width: 120px;
        height: 40px;
        font-size: 12px;
        padding: 2px 10px;
        font-weight: bold;
        color: $silver;
        background-color: transparent;
        cursor: pointer;
        border: 1px solid $silver;
      }
      input {
        outline: none;
        background-color: transparent;
      }
      .inputName {
        float: left;
        height: 30px;
        border-top: none;
        border-left: none;
        border-right: none;

        ::placeholder {
          opacity: .5;
        }
        label {
          float: left;
        }
      }
      .inputEmail {
        float: right;
        ::placeholder {
          opacity: .5;
        }
        label {
          float: left;
        }
      }
      .newMessage {
        float: left;
        height: 50px;
        padding-top: 15px;

        label {
          float: left;
        }
        textarea {
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid $silver;
          background-color: transparent;
          opacity: .4;
          outline: none;
        }
      }
    }
  }
  .footer {
    width: 100vw;
    height: 30px;
    color: $silver;
    .socialMedia {
      color: $silver;
      width: 75px;
      float: right;
      display: flex;
      justify-content: flex-end;
      opacity: .7;
      .insta {
        width: 20px;
        height: 20px;
        background-image: url("../../assets/Instagram.svg");
        background-repeat: no-repeat;
        background-size: contain;
      }
      .fb {
        width: 20px;
        height: 20px;
        background-image: url("../../assets/Facebook.svg");
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }



